<template>
  <!-- 人才搜索 -->
  <div class="telentSearch_wrapper">
    <!-- 顶部条件搜索项 -->
    <div class="telentsSearch_selectBar">
      <div class="selectBar_wrapper">
        <div class="joinIndustry_select select_box">
          <p @click="joinIndustryClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 1}">从事行业</span>
            <img
              :src="tabActiveIndex == 1 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt="下箭头图标"
            >
          </p>
        </div>
        <div class="wantCity_select select_box">
          <p @click="wantCityClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 2}">意向城市</span>
            <img
              :src="tabActiveIndex == 2 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt="下箭头图标"
            >
          </p>
        </div>
        <div class="moreChoice_select select_box">
          <p @click="moreChoiceClk">
            <span class="f-28 c-12221F" :class="{active: tabActiveIndex == 3}">更多</span>
            <img
              :src="tabActiveIndex == 3 ? selectArrowArr[0].select : selectArrowArr[0].normol"
              alt="下箭头图标"
            >
          </p>
        </div>
      </div>
    </div>
    <!-- 人才搜素列表 -->
    <div
      v-if="allIsResume"
      class="mt-73 telentsSerach_list"
      id="commonListTab_cnt"
      ref="commonListTab_cnt"
      :class="{'mt-52' : dataIdCltMt == 2}"
    >
      <ul class="common_list">
        <li
          class="drEach_li"
          @click="listClkFn"
          v-for="(item, index) in queryResumeList"
          :key="index"
          :id="item.id"
          style="margin-top:.9375rem"
        >
          <div class="left">
            <div class="leftImg">
              <!-- <img class="headPic" src="../../assets/user.png" alt="用户头像">
              <img class="genderPic" src="../../assets/man_symbol.png" alt="男性符号">-->
              <img
                class="headPic"
                v-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger)<= 24)"
                :src="resumePicOne"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34) || item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == undefined || item.genderString == null || item.genderString == '') && (item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicThree"
              >
              <img
                class="headPic"
                v-else-if="(item.genderString == '男' || item.genderString == undefined || item.genderString == null || item.genderString == '') && Number(item.bornInteger) >= 45"
                :src="resumePicFour"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '女' && ( Number(item.bornInteger)<= 24)"
                :src="resumePicFive"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '女' && ((Number(item.bornInteger) >= 25 && Number(item.bornInteger) <= 34 )|| item.bornInteger == undefined || item.bornInteger == null || item.bornInteger == '')"
                :src="resumePicSix"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '女' && (Number(item.bornInteger) >= 35 && Number(item.bornInteger) <= 44)"
                :src="resumePicServen"
              >
              <img
                class="headPic"
                v-else-if="item.genderString == '女' && Number(item.bornInteger) >= 45"
                :src="resumePicEight"
              >
              <img
                v-if="item.genderString == '男'"
                class="genderPic"
                src="../../assets/man_symbol.png"
                alt="男性符号"
              >
              <img v-else class="genderPic" src="../../assets/woman_symbol.png" alt="女性符号">
            </div>
          </div>
          <div class="right">
            <div class="top newPadding">
              <p class="lh-1 mb-19">
                <span class="flex_layout">
                  <span class="name">{{item.name}}</span>
                  <span class="age" v-if="item.bornInteger != undefined">{{item.bornInteger}}岁</span>
                  <span v-else class="age">暂无</span>
                  <span class="c-354242 f-30 ml-5" v-if="item.education != ''">{{item.education}}</span>
                  <span v-else class="c-354242 f-30 ml-5">暂无</span>
                </span>
              </p>
              <p class="text-left lh-1 dspFlex mb-20">
                <img class="deliver_icon" src="../../assets/manMin_pic.png" alt="在线图标">
                <span class="deliver_txt gray-818D8D">
                  在找
                  <span
                    class="black-354242"
                    v-if="item.intentionPosition != null && item.intentionPosition != undefined && item.intentionPosition.length == 0"
                  >暂时没有</span>
                  <span
                    class="black-354242"
                    v-else
                    v-for="(item, index) in item.intentionPosition"
                    :key="index"
                  >{{item.positionName}}</span>
                </span>
              </p>
              <p class="text-left lh-1 dspFlex">
                <img class="intention_icon" src="../../assets/heart_add.png" alt="意向图标">
                <span class="intention_txt gray-818D8D">
                  意向
                  <span
                    v-if="item.intentionalCity != null && item.intentionalCity != undefined && item.intentionalCity.length == 0"
                    class="black-354242"
                  >暂时没有</span>
                  <!-- <span v-else class="black-354242" v-for="(item, index) in item.intentionalCity" :key="index">{{item.intentionalPlace}}</span>                   -->
                  <span
                    v-else-if="item.intentionalCity.length > 1 && item.intentionalCity[0].intentionalPlace != ''"
                    class="black-354242"
                  >{{item.intentionalCity[0].intentionalPlace}}...</span>
                  <span
                    v-else-if="item.intentionalCity.length > 1 && item.intentionalCity[0].intentionalPlace == ''"
                    class="black-354242"
                  >{{item.intentionalCity[1].intentionalPlace}}...</span>
                  <span v-else class="black-354242">{{item.intentionalCity[0].intentionalPlace}}</span>
                </span>
              </p>
            </div>
          </div>
          <div class="getTel_btn">
            <p class="txt_wrapper">
              <span
                :id="gernerateId(item.id)"
                @click.stop="getTelInfo(item.id)"
                v-if="item.mobile == 'NA'"
              >获取联系方式</span>
              <span v-else>{{item.mobile}}</span>
            </p>
          </div>
        </li>
      </ul>
      <!-- 当列表数组length小于10,显示此暂无数据提示框,（滚动加载组件数组length小于10无暂无数据提示框） -->
      <div class="infinite-status-prompt text-center" v-if="queryResumeList.length < 10">
        <span>数据已全部加载</span>
      </div>
      <!-- 无限滚动加载组件 -->
      <infinite-loading @infinite="telentSearchListInfiniteFn" v-if="queryResumeList.length > 10">
        <span class="infinite-load">
          <i class="el-icon-loading"></i>
          <span>正在加载~</span>
        </span>
        <span class="infinite-load" slot="no-more">数据已全部加载</span>
      </infinite-loading>
    </div>
    <div v-else class="blank_page">没有搜到相关信息</div>
    <!-- 上滑modal组件 从事行业-->
    <slideModal-mdl
      ref="slideModalMdl"
      v-show="slideModalShow"
      @slideModalMaskFn="listenTabActiveIndexFn"
    ></slideModal-mdl>
    <!-- 上滑modal组件 意向城市-->
    <slideModalCity-mdl
      ref="slideModalCityMdl"
      v-show="slideModalCityShow"
      @slideModalMaskFn="listenTabActiveIndexFn"
    ></slideModalCity-mdl>
    <!-- 人才搜索右侧popup -->
    <mt-popup v-model="popupVisible" position="right" :modal="false">
      <div class="rightPop_box">
        <!-- 年龄 -->
        <div class="agePop_box">
          <p @click="ageBarClk">
            <span class="f-28 c-0B231F">年龄</span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="灰色向上箭头">
            </span>
          </p>
          <div class="age_selectBox flexLayout" v-show="ageCntShow">
            <p>
              <input
                type="tel"
                placeholder="最小年龄"
                v-model="minAgeVal"
                class="text-center ageIpt"
                maxlength="2"
                @input="ageIptChgeFn"
              >
            </p>
            <div></div>
            <p>
              <input
                type="tel"
                :placeholder="maxAgePhlTxt"
                v-model="maxAgeVal"
                class="text-center ageIpt"
                maxlength="2"
              >
            </p>
          </div>
          <ul class="flexLayout mb-100" v-show="ageCntShow">
            <li
              :class="{li_active: ageLiActive == index}"
              v-for="(item,index) in ageArr"
              :key="index"
              @click="ageLiClk(index)"
              class="mb-100"
            >{{item}}</li>
          </ul>
        </div>
        <!-- 性别 -->
        <div class="genderPop_box">
          <p @click="genderBarClk">
            <span class="f-28 c-0B231F">性别</span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="灰色向上箭头">
            </span>
          </p>
          <ul class="flexLayout mb-100" v-show="genderCntShow">
            <li
              :class="{li_active: genderLiActive == index}"
              v-for="(item,index) in genderArr"
              :key="index"
              @click="genderLiClk(index)"
            >{{item}}</li>
          </ul>
        </div>
        <!-- 学历 -->
        <div class="genderPop_box">
          <p @click="degreeBarClk">
            <span class="f-28 c-0B231F">学历</span>
            <span>
              <img src="../../assets/grayBelow_arrow.png" alt="灰色向上箭头">
            </span>
          </p>
          <ul class="flexLayout mb-100 degree_ul" v-show="degreeCntShow">
            <li
              :class="{li_active: degreeLiActive == index}"
              v-for="(item,index) in degreeArr"
              :key="index"
              @click="degreeLiClk(index)"
            >{{item}}</li>
          </ul>
        </div>
        <!-- 底部按钮 -->
        <div class="footerBtn">
          <button @click="resetBtnClk">重置</button>
          <button @click="ensureBtnClk">确认</button>
        </div>
      </div>
    </mt-popup>
    <!-- 右侧popup的mask -->
    <div class="mask" v-show="maskShow" @click="maskPopUpClk"></div>
    <!-- 人才搜索积分扣除modal -->
    <div class="score_modal" v-show="jMarkModalShow">
      <!-- <p class="f-28 c-12221F mb-2 lh-1 mb-10">您好,本次操作需要50个积分点,</p>
      <p class="f-28 c-12221F lh-1">操作成功后相关简历将进入您的简历库?</p> -->
      <mt-checklist v-model="value" :options="options" @change="checkon"></mt-checklist>
      <!-- <div class="modal_footerBtn">
        <div class="btn" @click="jModalCancelBtnClk">取消</div>
        <div class="btn" @click="jModalSureBtnClk">确定</div>
      </div> -->
    </div>
    <!-- 积分不足modal -->
    <div class="score_modal" v-show="showIntegration">
      <p class="f-28 c-12221F mb-2 lh-1 mb-10" style="margin-top:1.9375rem">您好,您的积分不足,请先购买积分</p>
      <div class="modal_footerBtn">
        <div class="btn" @click="falseIntegration">我在想想</div>
        <div class="btn" @click="trueIntegration">去购买</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Util from "@/js/mUtils";
import slideModal from "@/views/resume/slideModal"; // 上滑modal组件 从事行业modal
import slideModalCity from "@/views/resume/slideModalCity"; // 上滑modal组件-意向城市modal
import { Popup } from "mint-ui"; // mint-ui pop
Vue.component(Popup.name, Popup);
import { Checklist } from "mint-ui"; // mint-ui CheckList
Vue.component(Checklist.name, Checklist);
import InfiniteLoading from "vue-infinite-loading"; // 无限滚动加载组件
// 我的简历用户头像
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
import { Notify } from "vant"; // toast提示
import { Indicator } from 'mint-ui'; // mint-ui 加载中提示

export default {
  data() {
    return {
      globalApiUrl: domain.testUrl, // 全局域名
      qdsGlobalApiUrl: domain.qdsDomain, // 全局域名
      searchGlobalApiUrl: domain.searchTestUrl, // 人才搜索全局域名
      slideModalShow: false, // 上滑modal默认隐藏
      slideModalCityShow: false, // 意向城市默认隐藏
      tabActiveIndex: "", // 人才搜索顶部select搜索条件选中状态
      selectArrowArr: [
        // 人才搜索顶部select图标
        {
          normol: require("../../assets/below_arrow.png"), // 默认图标
          select: require("../../assets/activeBelow_arrow.png") // 选中图标
        }
      ],
      popupVisible: false, // 右侧pop默认隐藏
      ageArr: [
        "16岁及以上",
        "16岁-25岁",
        "26岁-35岁",
        "36岁-45岁",
        "46岁-55岁",
        "55岁以上"
      ], // 右侧pop年龄数组
      ageLiActive: 0, // age默认选中16岁及以上
      minAgeVal: "16", // 最小年龄输入框值
      maxAgeVal: "", // 最大年龄输入框值
      maxAgePhlTxt: "最大年龄", // 最大年龄placeholder
      genderArr: ["不限", "男", "女"], // 右侧pop性别数组
      genderLiActive: 0, // gender默认选中不限
      degreeArr: [
        // 学历
        "不限",
        "小学",
        "初中",
        "中专",
        "高中",
        "大专",
        "本科",
        "硕士",
        "博士"
      ], // 右侧pop学历数组
      degreeLiActive: 0, // degree默认选中不限
      maskShow: false, // popup的mask
      value: [""], // mint-ui checkList  值默认为空,不选中状态。不要赋值否则默认为选中状态
      options: [
        {
          label: "知道啦，以后不用提示了哦",
          value: "A"
        }
      ],
      // 人才搜索接口传递参数
      pageNum: 1, // 页码默认第一页
      positionCode: "", // 从事行业
      intentionalPlace: "", // 意向城市
      education: "", // 学历
      genderString: "", // 性别
      bornIntegerMin: "", // 年龄最小值
      bornIntegerMax: "", // 年龄最大值
      allIsResume: true, // 列表有数据否则显示空页面
      idArr: [], // 预存每个简历的id
      indexArr: [], // 查看手机号码的简历下标数组
      queryResumeList: [], // 人才搜索列表数组(页面遍历)
      // 我的简历用户头像
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      rightPopUpAgeLiClkFlag: "", // 年龄li点击标记
      minAgeIptChangeFlag: "", // 最小年龄输入框input事件标记
      ageCntShow: true, // 右侧popup年龄区域不显示
      genderCntShow: false, // 右侧popup性别区域不显示
      degreeCntShow: false, // 右侧popup学历区域不显示
      certificationStatus: "", // 存储获取的认证状态
      jMarkModalShow: false, // 积分消耗提醒modal默认隐藏
      showIntegration: false, //积分不足提示弹窗
      resumeId: "",
      resumeIndex: "",
      dataIdCltMt: "", // 调整commonListTab_cnt的margin-top
      getUserStorageId:'',  // 存储获取storage的用户ID
      changeFlag: true, // 积分扣除modal的单选框点击标记
    };
  },
  components: {
    "slideModal-mdl": slideModal, // 从事行业modal组件注册
    "slideModalCity-mdl": slideModalCity, // 意向城市modal组件注册
    InfiniteLoading, // 滚动无限加载组件
    Notify // toast提示
  },
  mounted() {
    this.getTelentSearchListData(); // 调用获取人才搜索接口
    this.$refs.commonListTab_cnt.classList.add("mt-52");   
  },
  methods: {    
    // 知道啦以后不用提示了哦change事件 用户选中checkBox登陆后禁止显示积分扣除modal
    checkon(){ 
      this.changeFlag = !this.changeFlag; // 点击切换标记
      if(this.changeFlag == false){  // 已选中
          this.value = ["A"];        
      }
      
      // // 点击选中赋值为A,若为A本地存储用户ID,否则从本地存储中移除用户ID。获取联系方式按钮（getTelInfo事件）取出用户ID                   
      // if(this.value[0] == 'A'){
      //   // 选中状态
      //   localStorage.setItem("userCheckBoxShwOrNt", this.getUserStorageId); // userId存storage,积分消耗modal是否显示依据
      // }else{
      //   // 未选中
      //   localStorage.removeItem("userCheckBoxShwOrNt", this.getUserStorageId); // 移除storage
      // }
    },
    // 人才搜索-从事行业点击事件
    joinIndustryClk() {
      this.tabActiveIndex = 1; // 此select被点亮
      this.slideModalShow = true; // 上滑modal显示
      this.$refs.slideModalMdl.slideUpModalShow = true; // 控制上滑modal子组件
      this.$refs.slideModalMdl.maskShow = true; // 控制上滑modal子组件
      // 组件全部加载获取sildeModal组件中左侧列表height,赋值右侧列表maxHeight
      this.$nextTick(function() {
        let leftListHeight = this.$refs.slideModalMdl.$el.children[0]
          .children[1].offsetHeight; // 获取slideModal中左侧列表height
        let rightListDom = this.$refs.slideModalMdl.$el.children[0].children[1]
          .children[1]; // 获取slideModal中右侧列表DOM
        rightListDom.style.maxHeight = leftListHeight + "px";
        // let rightListDom1 = this.$refs.slideModalMdl.$el.children[0].children[1]
        //   .children[2]; // 获取slideModal中右侧列表DOM
        // rightListDom1.style.maxHeight = leftListHeight + "px";
      });
    },
    // 人才搜索-意向城市点击事件
    wantCityClk() {
      this.tabActiveIndex = 2; // select被点亮
      this.slideModalCityShow = true; // 上滑modal显示
      this.$refs.slideModalCityMdl.slideUpModalShow = true; // 控制上滑modal子组件
      this.$refs.slideModalCityMdl.maskShow = true; // 控制上滑modal子组件
      // 组件全部加载获取sildeModal组件中左侧列表height,赋值右侧列表maxHeight
      this.$nextTick(function() {
        let leftListHeight = this.$refs.slideModalCityMdl.$el.children[0]
          .children[1].offsetHeight; // 获取slideModal中左侧列表height
        let rightListDom = this.$refs.slideModalCityMdl.$el.children[0]
          .children[1].children[1]; // 获取slideModal中右侧列表DOM
        rightListDom.style.maxHeight = leftListHeight + "px";
        // let rightListDom1 = this.$refs.slideModalCityMdl.$el.children[0]
        //   .children[1].children[2]; // 获取slideModal中右侧列表DOM
        // rightListDom1.style.maxHeight = leftListHeight + "px";
      });
    },
    // 人才搜索-更多点击事件
    moreChoiceClk() {
      this.tabActiveIndex = 3; // select被点亮
      this.popupVisible = true; // 右侧pop显示
      this.maskShow = true; // 右侧popup-mask显示
    },
    // popup-mask的点击事件
    maskPopUpClk() {
      this.popupVisible = false; // 右侧pop隐藏
      this.maskShow = false; // 右侧popup-mask隐藏
      this.tabActiveIndex = 0; // select初始化
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.showIntegration = false; // 关闭积分不足modal
      this.value = [""];  // mask点击移除勾选的不再显示积分扣除
    },
    // 右侧popUp的年龄输入框input事件
    ageIptChgeFn() {
      this.ageLiActive = 99; // 取消默认选中16岁以以上
      if (this.minAgeVal == "") {
        // 最小年龄不填写默认选中16岁及以上

        //  this.ageLiActive = 0;  // 选中16岁及以上
        this.bornIntegerMin = 16; // 最小年龄不填写默认往后台传参bornIntegerMin=16
      } else {
        // 用户输入最小年龄
        this.minAgeIptChangeFlag = 1; // 标记用户已自定义最小年龄值
      }
    },
    // 右侧pop 年龄li点击事件
    ageLiClk(index) {
      this.ageLiActive = index;
      if (index == 0) {
        // 16岁及以上
        this.minAgeVal = 16; // minAgeVal最小年龄v-model 赋值16
        this.maxAgeVal = ""; //  maxAgeVal 最大年龄v-model赋值空显示placeholder
        this.maxAgePhlTxt = "最大年龄"; // maxAgePhlTxt最大年龄placeholder
        this.bornIntegerMin = 16; //  人才列表接口最小年龄参数 赋值16
        this.rightPopUpAgeLiClkFlag = 0; // 赋值年龄li点击标记
        this.bornIntegerMax = ""; // 人才列表接口最大年龄参数 赋值空
      } else if (index == 1) {
        // 16岁-25岁
        this.minAgeVal = 16;
        this.maxAgeVal = 25;
