    // 右侧popup重置按钮点击事件  选项初始化
    resetBtnClk() {
      this.ageLiActive = 0; // 年龄默认选中16岁以以上
      this.minAgeVal = 16; // 最小年龄v-model
      this.maxAgeVal = ""; // 最大年龄v-model
      this.maxAgePhlTxt = "最大年龄"; // 最大年龄plceholder
      this.genderLiActive = 0; // 初始化性别条件
      this.degreeLiActive = 0; // 初始化学历条件
      this.genderString = ""; // 初始化后台传递选中性别字段
      this.education = ""; // 初始化后台传递选中学历字段
      this.bornIntegerMin = ""; //  初始化后台传递年龄最小字段
      this.bornIntegerMax = ""; //  初始化后台传递年龄最大字段
      this.rightPopUpAgeLiClkFlag = ""; // 初始化年龄li点击标记,目的重置bornIntegerMax
    },
    // 右侧popup确定按钮点击事件
    ensureBtnClk() {
      // 性别学历接口OK

      // this.popupVisible = false; // 右侧popup隐藏
      // this.maskShow = false; // 右侧popup-mask隐藏
      this.tabActiveIndex = 0; // 顶部select字体颜色图标初始化
      this.pageNum = 1; // 人才列表接口参数 页码字段 初始化为1

      if (this.minAgeIptChangeFlag == 1) {
        // 为1表示用户已自定义最小年龄
        this.bornIntegerMin = this.minAgeVal; // 后台传递字段等于用户自定义最小年龄
      } else {
        this.bornIntegerMin = 16; // 用户未自定义最小年龄，默认往后台传递参数最小年龄16
      }

      // 不同年龄点击判断,重新赋值bornIntegerMin传参至后台 (赋值前默认bornIntegerMin总是16)
      if (
        this.rightPopUpAgeLiClkFlag == 1 ||
        this.rightPopUpAgeLiClkFlag == 0
      ) {
        this.bornIntegerMin = 16; // 选中16岁以上
      } else if (this.rightPopUpAgeLiClkFlag == 2) {
        // 选中26-35岁
        this.bornIntegerMin = 26;
      } else if (this.rightPopUpAgeLiClkFlag == 3) {
        // 选中36-45
        this.bornIntegerMin = 36;
      } else if (this.rightPopUpAgeLiClkFlag == 4) {
        // 选中46-55
        this.bornIntegerMin = 46;
      } else if (this.rightPopUpAgeLiClkFlag == 5) {
        // 选中55
        this.bornIntegerMin = 55;
      }

      // 最大最小年龄逻辑判断
      if (this.minAgeVal != "" || this.maxAgeVal != "") {
        // 最大最小年龄不为空进行数字类型验证
        if (isNaN(this.minAgeVal) != false || isNaN(this.maxAgeVal) != false) {
          // 最大最小年龄不是数字类型
          Notify({
            message: "年龄必须为数字",
            duration: 1500,
            background: "#FF6262"
          });
        } else {
          // 是数字类型
          if (this.maxAgeVal != "") {
            // 最大年龄不为空才提示 最小年龄不能大于最大年龄
            if (this.minAgeVal > Number(this.maxAgeVal)) {
              Notify({
                message: "最小年龄不能大于最大年龄",
                duration: 1500,
                background: "#FF6262"
              });
            } else {
              // 最大年龄大于最小年龄
              this.popupVisible = false; // 右侧popup隐藏
              this.maskShow = false; // 右侧popup-mask隐藏
              this.bornIntegerMin = this.minAgeVal; // 取最小年龄合法数据赋值接口传参字段
              this.bornIntegerMax = this.maxAgeVal; // 取最大年龄合法数据赋值接口传参字段
              this.getTenlentSearchDataHasOptionsFn(); // 调用 人才搜索列表接口调用函数(有条件搜索)
            }
          } else {
            // 最大年龄为空,不提示 最小年龄不能大于最大年龄
            this.popupVisible = false; // 右侧popup隐藏
            this.maskShow = false; // 右侧popup-mask隐藏
          }
        }
      } else {
        // 年龄输入框都为空
      }

      this.getTenlentSearchDataHasOptionsFn(); // 调用 人才搜索列表接口调用函数(有条件搜索)
    },
