          if (response.data.code == 200) {
            if (response.data.data.dataList.length > 0) {
              that.queryResumeList = []; // 重定义页面列表遍历数组(存新搜索结果数据)
              that.allIsResume = true;
              var dataArr = response.data.data.dataList;
              that.pageNum = response.data.data.pageNum; // 获取接口页码
              dataArr.forEach((item, index) => {
                if (item.intentionalCity) {
                  item.intentionalCity.forEach(citys => {
                    // 意向城市数据
                    if (citys.intentionalPlace) {
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null /g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        / null/g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null/g,
                        ""
                      );
                      if (citys.intentionalPlace == " ") {
                        citys.intentionalPlace = citys.intentionalPlace;
                      } else {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                } else {
                  item.intentionalCity = [];
                }
                if (item.intentionPosition) {
                  item.intentionPosition.forEach(positions => {
                    // 意向岗位数据
                    if (positions.positionName) {
                      positions.positionName = positions.positionName.replace(
                        /null /g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        / null/g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        /null/g,
                        ""
                      );
                      if (positions.positionName == " ") {
                        positions.positionName = positions.positionName;
                      } else {
                        positions.positionName = positions.positionName.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                } else {
                  item.intentionPosition = [];
                }
              });
              that.queryResumeList = that.queryResumeList.concat(dataArr); // 搜索结果数据存于页面遍历数组
            } else if (response.data.data.dataList.length == 0) {
              Notify({
                message: "查询结果无数据",
                duration: 1500,
                background: "#FF6262"
              });
              that.allIsResume = false; // 无数据展示空页面
            }
          } else {
            // 状态码非200
            that.allIsResume = false; // 无数据展示空页面
          }
        })
        .catch(function() {
          Notify({
            message: "系统异常",
            duration: 1500,
            background: "#FF6262"
          });
        });
    },
    // 右侧popUp年龄点击折叠
    ageBarClk() {
      this.ageCntShow = !this.ageCntShow;
    },
    // 右侧popUp性别点击折叠
    genderBarClk() {
      this.genderCntShow = !this.genderCntShow;
    },
    // 右侧popup学历点击折叠
    degreeBarClk() {
      this.degreeCntShow = !this.degreeCntShow;
    },
    // 获取联系方式点击事件
    gernerateId(resumeId) {
      // 动态增加id
      return resumeId;
    },
    // 获取联系方式点击事件
    getTelInfo(resumeId) {
      this.resumeId = resumeId;
      this.certificationStatus = sessionStorage.getItem("qdsMCertificationStatus");   
      this.getUserStorageId = localStorage.getItem("qdsSCorpUserId"); // 获取本地存储当前用户ID          

      // 获取认证状态 1未认证  2认证中  3成功  4失败
      if (this.certificationStatus == 1) {
        // 未认证跳转认证信息填写页面
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 2) {
        // 认证中 跳转至“企业认证-等待审核”页面 OK
        this.$router.push("/qdsAttestation");
      } else if (this.certificationStatus == 3) {
        // 认证成功 显示消耗积分提示(若用户已勾选不再显示,则不再显示积分扣除modal)         

        // 判断userCheckBoxShwOrNt(值为用户ID)中是否有当前用户ID,有-不再显示积分扣除modal
        var cutDownJMarkFlag = localStorage.getItem('userCheckBoxShwOrNt'); // 取出当前用户ID                
        if(cutDownJMarkFlag == this.getUserStorageId){  // this.getUserStorageId 获取联系方式按钮点击获取到的用户ID          
          this.jMarkModalShow = false; // 不显示积分消耗提示modal
          this.maskShow = false; // 不显示mask
          this.jModalSureBtnClk(); // 扣除积分modal不显示,调用扣除modal上的点击事件
        }else{          
          this.jMarkModalShow = true; // 显示积分消耗提示modal
          this.maskShow = true; // 显示mask
        }
        
      } else if (this.certificationStatus == 4) {
        // 认证失败 跳转至认证页面
        this.$router.push("/qdsAttestation");
      }
    },
    // 积分modal的取消按钮点击事件
    jModalCancelBtnClk() {
      this.jMarkModalShow = false; // 关闭积分消耗提示modal
      this.maskShow = false; // 关闭mask
      this.value = [""];  // 积分扣除modal点击取消,移除选中的不在显示单选框
    },
    // 积分modal的确定按钮点击事件
    jModalSureBtnClk() {
      // loading
        Indicator.open({
          text: '请求中...',
          spinnerType: 'snake'
        });

        this.jMarkModalShow = false; // 隐藏积分消耗提示modal
        this.maskShow = false; // 隐藏mask

      
      // 用户已勾选单选框 不再显示积分扣除modal
      if(this.value[0] == 'A'){
        // 选中状态
        localStorage.setItem("userCheckBoxShwOrNt", this.getUserStorageId); // userId存storage,积分消耗modal是否显示依据
      }


      //购买简历
      var that = this;
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      that.idArr.forEach((item, index) => {
        // 放置有多个id的情况
        if (that.resumeId == item) {
          that.resumeIndex = index;
        }
      });
      that
        .axios(
          that.qdsGlobalApiUrl + "channelResume/followResume?token=" + token,
          {
            method: "post",
            params: {
              userId: userId,
              resumeId: that.resumeId,
              source: "search"
            },
            timeout: 10000
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: "简历获取成功，请到“我的简历”查看",
              duration: 1500,
              background: "#20D3B3"
            });
            that.queryResumeList[that.resumeIndex].mobile =
              response.data.data.mobile;
            that.queryResumeList[that.resumeIndex].name =
              response.data.data.name;

            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.maskShow = false; // 关闭mask
          } else if (response.data.code == 30002) {
             Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: "积分不足",
              duration: 1500,
              background: "#FF6262"
            });
            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.showIntegration = true; // 显示积分不足modal
            that.maskShow = true; // 显示maks
          } else {
             Indicator.close(); // 数据请求成功隐藏loading
            Notify({
              message: response.data.message,
              duration: 1500,
              background: "#FF6262"
            });
            that.jMarkModalShow = false; // 关闭积分消耗提示modal
            that.maskShow = false; // 关闭mask
          }
        })
        .catch(function(error) {
           Indicator.close(); // 数据请求成功隐藏loading
           Notify({
              message: '请求失败',
              duration: 1500,
              background: '#FF6262'
          });
          this.value = [""];  // 积分扣除modal点击取消,移除选中的不在显示单选框

          // that.jMarkModalShow = false; // 关闭积分消耗提示modal
          // that.maskShow = false; // 关闭mask
        });
    },
    falseIntegration() {
      // 积分不足 取消按钮
      this.showIntegration = false;
      this.maskShow = false;
    },
    trueIntegration() {
      // 积分不足 去充值按钮
      this.showIntegration = false;
      this.maskShow = false;
      this.$router.push("/qdsPersonal"); // 跳转到充值页面
    }
  }
};
</script>
