<template>
  <!-- 投递记录列表 -->
  <div class="deliverRecoredList_wrapper commonListTab_cnt">
    <!-- 列表有数据并且认证状态为认证通过 -->
    <div v-if="listPageShow && certificationStatus == 3">
      <ul class="common_list">
        <li
          class="drEach_li"
          @click="liClk"
          v-for="(item,index) in optionResume"
          :key="index"
          :id="item.resume.resumeId"
        >
          <div class="left">
            <div class="leftImg">
              <!-- <img class="headPic" src="../../assets/user.png" alt="用户头像">
              <img class="genderPic" src="../../assets/man_symbol.png" alt="男性符号">-->
              <img
                class="headPic"
                v-if="(item.resume.genderString== '男' || item.resume.genderString == '暂无') && (Number(item.resume.bornInteger)<= 24)"
                :src="resumePicOne"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '男' || item.resume.genderString == '暂无') && ((Number(item.resume.bornInteger) >= 25 && Number(item.resume.bornInteger) <= 34) || item.resume.bornInteger == '暂无')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '暂无') && (item.resume.bornInteger == '暂无')"
                :src="resumePicTwo"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '男' || item.resume.genderString == '暂无') && (Number(item.resume.bornInteger) >= 35 && Number(item.resume.bornInteger) <= 44)"
                :src="resumePicThree"
              >
              <img
                class="headPic"
                v-else-if="(item.resume.genderString == '男' || item.resume.genderString == '暂无') && Number(item.resume.bornInteger) >= 45"
                :src="resumePicFour"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '女' && ( Number(item.resume.bornInteger)<= 24)"
                :src="resumePicFive"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '女' && ((Number(item.resume.bornInteger) >= 25 && Number(item.resume.bornInteger) <= 34 )|| item.resume.bornInteger == '暂无')"
                :src="resumePicSix"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '女' && (Number(item.resume.bornInteger) >= 35 && Number(item.resume.bornInteger) <= 44)"
                :src="resumePicServen"
              >
              <img
                class="headPic"
                v-else-if="item.resume.genderString == '女' && Number(item.resume.bornInteger) >= 45"
                :src="resumePicEight"
              >

              <img
                v-if="item.resume.genderString == '男'"
                class="genderPic"
                src="../../assets/man_symbol.png"
                alt="男性符号"
              >
              <img v-else class="genderPic" src="../../assets/woman_symbol.png" alt="女性符号">
            </div>
          </div>
          <div class="right">
            <div class="top">
              <p class="lh-1 mb-19">
                <span class="flex_layOut">
                  <span class="name">{{item.resume.resumeName}}</span>
                  <span class="c-354242 f-15 mr-8">{{item.resume.genderString}}</span>
                  <span class="age">{{item.resume.bornInteger}}</span>
                </span>
                <!-- <span class="deliveryState">{{item.resume.source}}</span> -->
                <!-- 投递来源暂时隐藏 -->
              </p>
              <p class="text-left mb-21 flex_layout lh-1">
                <img class="deliver_icon" src="../../assets/fly.png" alt="投递图标">
                <span class="deliver_txt gray-818D8D flex_layout">
                  <span class="deliverTxt_w">投递</span>
                  <span class="black-354242 deliver_position ml-10">{{item.demand.jobTypeString}}</span>
                </span>
              </p>
              <p class="text-left flex_layout lh-1">
                <img class="intention_icon" src="../../assets/heart_add.png" alt="意向图标">
                <span class="intention_txt gray-818D8D">
                  意向
                  <span
                    class="black-354242"
                    v-for="(item,index) in item.resume.intentionalCity"
                    :key="index"
                  >{{item.intentionalPlace}}</span>
                </span>
              </p>
            </div>
            <div class="bottom text-left">投递时间:{{item.applyTime}}</div>
          </div>
        </li>
      </ul>
      <!-- 当列表数组length小于10,显示此暂无数据提示框,（滚动加载组件数组length小于10无暂无数据提示框） -->
      <div class="infinite-status-prompt text-center" v-if="optionResume.length < 10">
        <span>数据已全部加载</span>
      </div>
      <!-- 无限滚动加载组件 -->
      <infinite-loading @infinite="deliverRecordListInfiniteFn" v-if="optionResume.length >= 10">
        <!-- 默认length是10   v-if="optionResume.length >= 10"-->
        <span class="infinite-load">
          <i class="el-icon-loading"></i>
          <span>正在加载呢~</span>
        </span>
        <span slot="no-more">数据已全部加载</span>
      </infinite-loading>
    </div>
    <!-- 列表无数据 投递记录空页面 -->
    <div v-else-if="listPageShow == false" class="blank_page">
      <img src="../../assets/deliver_blank.png" alt="投递空图片">
      <p>亲爱的, 您今日还没有投递的简历先别急, 再等等~</p>
    </div>
    <!-- 认证状态为未认证、认证中、认证失败 -->
    <div
      v-else-if="certificationStatus == 1 || certificationStatus == 2 || certificationStatus == 4"
      class="auth-no-success"
    >
      <img :src="recruitingListBack" class="bo-data-icon">
      <p class="recruiting-tips">企业认证成功后，即可解锁该模块哦~</p>
    </div>
  </div>
</template>

<script>
import Util from "@/js/mUtils";
import InfiniteLoading from "vue-infinite-loading"; // 无限滚动加载组件
// 我的简历用户头像
import resumePicOne from "@/assets/post/boy_1.png";
import resumePicTwo from "@/assets/post/boy_2.png";
import resumePicThree from "@/assets/post/boy_3.png";
import resumePicFour from "@/assets/post/boy_4.png";
import resumePicFive from "@/assets/post/girl_1.png";
import resumePicSix from "@/assets/post/girl_2.png";
import resumePicServen from "@/assets/post/girl_3.png";
import resumePicEight from "@/assets/post/girl_4.png";
import recruitingListBack from "@/assets/post/recruiting_list_back.png"; //空白icon

export default {
  data() {
    return {
      listPageShow: true, // 列表有数据
      searchParams: {
        // 投递记录列表传递参数
        jobTypeCode1: "",
        jobTypeCode2: "",
        jobTypeCode3: "",
        resumeName: "",
        resumeId: "",
        corpId: ""
      },
      currentPage: 1, // 投递记录接口请求传递的参数 默认第一页
      globalApiUrl: domain.testUrl, // 全局域名
      qdsGlobalApiUrl: domain.qdsDomain, // 全局域名
      applyTime: [], // 投递时间
      textCont: [], // 关注, 感兴趣
      currentTextCont: "感兴趣", // 当前的关注状态
      applyTimeContent: "", // 投递时间
      dataListArr: [],
      optionResume: [], //列表基本信息,列表数据遍历来源
      optionDemand: {}, // 投递信息
      options: {},
      listId: 0, // 默认为0, 即默认显示第一条
      totalPage: 0, // 总页数
      // 我的简历用户头像
      resumePicOne: resumePicOne,
      resumePicTwo: resumePicTwo,
      resumePicThree: resumePicThree,
      resumePicFour: resumePicFour,
      resumePicFive: resumePicFive,
      resumePicSix: resumePicSix,
      resumePicServen: resumePicServen,
      resumePicEight: resumePicEight,
      listPageState: false, //是否有分页
      certificationStatus: "", //认证状态
      recruitingListBack: recruitingListBack
    };
  },
  components: {
    InfiniteLoading
  },
  mounted() {
    this.certificationStatus = sessionStorage.getItem(
      "qdsMCertificationStatus"
    );
    if (this.$route.query.corpId) {
      this.searchParams.corpId = this.$route.query.corpId;
    }
    // 页面加载请求投递记录列表接口
    if (this.certificationStatus == 3) {
      this.deliverRecoredAllData();
    }
  },
  methods: {
    // 请求投递记录列表数据接口
    deliverRecoredAllData() {
      var that = this;
      var token = Util.getCookie("qdsMToken");
      var userId = localStorage.getItem("qdsSCorpUserId");
      that
        .axios(that.qdsGlobalApiUrl + "channelApply/applyList?token=" + token, {
          method: "get",
          params: {
            userId: userId,
            searchParams: that.searchParams, // 搜索条件
            page: that.currentPage, // 页码默认第一页
            size: 10 // 一页的数据量
          }
        })
        .then(function(response) {
          if (response.data.code == 200) {
            // 200接口调用成功
            var dataArr = response.data.data.applys;
            if (response.data.data.applys.length > 0) {
              that.totalPage = response.data.data.totalPage; // 获取接口数据的总页数
              if (that.totalPage > 1) {
                that.listPageState = true;
              } else if (that.totalPage <= 1) {
                that.listPageState = false;
              }
              dataArr.forEach((itemList, index) => {
                if (itemList.resume.intentionalCity) {
                  itemList.resume.intentionalCity.forEach(citys => {
                    // 意向城市数据
                    if (citys.intentionalPlace) {
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null /g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        / null/g,
                        ""
                      );
                      citys.intentionalPlace = citys.intentionalPlace.replace(
                        /null/g,
                        ""
                      );
                      if (citys.intentionalPlace == " ") {
                        citys.intentionalPlace = citys.intentionalPlace;
                      } else {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                }
                that.applyTime.push(itemList.applyTime); // 投递时间
                if (itemList.resume.isSeen == "02") {
                  that.textCont.push("已关注");
                } else if (itemList.resume.isSeen == "01") {
                  that.textCont.push("感兴趣");
                }

                if (itemList.resume.intentionPosition) {
                  itemList.resume.intentionPosition.forEach(positions => {
                    // 意向岗位数据
                    if (positions.positionName) {
                      positions.positionName = positions.positionName.replace(
                        /null /g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        / null/g,
                        ""
                      );
                      positions.positionName = positions.positionName.replace(
                        /null/g,
                        ""
                      );
                      if (positions.positionName == " ") {
                        positions.positionName = positions.positionName;
                      } else {
                        positions.positionName = positions.positionName.replace(
                          / /g,
                          "/"
                        );
                      }
                    }
                  });
                }
                // 招聘类型(1:社会工、2:实习工、3:临时工、4:不限、5:小时工、6:寒假工、7:暑假工、8：学生工)
                if (itemList.demand.recruitDemandType) {
                  if (itemList.demand.recruitDemandType == 1) {
                    itemList.demand.recruitDemandType = "社会工";
                  }
                  if (itemList.demand.recruitDemandType == 2) {
                    itemList.demand.recruitDemandType = "实习工";
                  }
                  if (itemList.demand.recruitDemandType == 3) {
                    itemList.demand.recruitDemandType = "临时工";
                  }
                  if (itemList.demand.recruitDemandType == 4) {
                    itemList.demand.recruitDemandType = "不限";
                  }
                  if (itemList.demand.recruitDemandType == 5) {
                    itemList.demand.recruitDemandType = "小时工";
                  }
                  if (itemList.demand.recruitDemandType == 6) {
                    itemList.demand.recruitDemandType = "寒假工";
                  }
                  if (itemList.demand.recruitDemandType == 7) {
                    itemList.demand.recruitDemandType = "暑假工";
                  }
                  if (itemList.demand.recruitDemandType == 8) {
                    itemList.demand.recruitDemandType = "学生工";
                  }
                }
                var objResume = itemList.resume;
                for (var element in objResume) {
                  if (element) {
                    if (
                      objResume[element] == "" ||
                      objResume[element] == null ||
                      objResume[element] == []
                    ) {
                      objResume[element] = "暂无";
                    }
                  } else {
                    console.log("缺失字段");
                  }
                }
                var objDemand = itemList.demand;
                for (var i in objDemand) {
                  if (i) {
                    if (
                      objDemand[i] == "" ||
                      objDemand[i] == null ||
                      objDemand[i] == []
                    ) {
                      objDemand[i] = "暂无";
                    }
                  } else {
                    console.log("缺失字段");
                  }
                }
              });
              that.currentTextCont = that.textCont[that.listId]; // textCont 关注感兴趣  currentTextCont:感兴趣字符串
              that.applyTimeContent = that.applyTime[that.listId];

              that.dataListArr = dataArr;
              that.options = that.dataListArr[that.listId];
              // that.optionResume = that.dataListArr[that.listId].resume; //基本信息
              that.optionResume = that.dataListArr; // 用于遍历列表数据optionResume

              that.optionDemand = that.dataListArr[that.listId].demand; // 投递信息
              that.listPageShow = true; // listPageShow页面有数据
            } else if (
              response.data.data.applys.length == 0 &&
              that.currentPage == 1
            ) {
              that.listPageShow = false;
            }
          }
        })
        .catch(function(error) {});
    },
    // 列表点击事件
    liClk(e) {
      this.$router.push("/qdsSearchResume/deliverRecoredDetial"); // 跳转投递记录详情页
      var that = this;
      var id = e.currentTarget.getAttribute("id"); // 当前用户resumeId
      localStorage.setItem("deliverRecordId", id); // 当前点击的用户resumeId
      var deliverRecordListString = JSON.stringify(that.optionResume); //  将JSON对象转化成字符串
      localStorage.setItem("deliverRecordList", deliverRecordListString); // 投递记录列表数据
    },
    // 列表无限滚动加载
    deliverRecordListInfiniteFn($state) {
      if (that.listPageState) {
        // listPageState为true有分页
        var that = this;
        var token = Util.getCookie("qdsMToken");
        var userId = localStorage.getItem("qdsSCorpUserId");
        that.currentPage = that.currentPage + 1;
        that
          .axios(
            that.qdsGlobalApiUrl + "channelApply/applyList?token=" + token,
            {
              method: "get",
              params: {
                userId: userId,
                searchParams: that.searchParams, // 搜索条件
                page: that.currentPage, // 页码默认第一页
                size: 10 // 一页的数据量
              }
            }
          )
          .then(response => {
            if (response.data.code == 200) {
              if (response.data.data.applys) {
                if (response.data.data.applys.length < 10) {
                  that.listPageState = false;
                } else {
                  that.listPageState = true;
                }

                var drlInfinitedListDataArr = response.data.data.applys;
                drlInfinitedListDataArr.forEach((itemList, index) => {
                  if (itemList.resume.intentionalCity) {
                    itemList.resume.intentionalCity.forEach(citys => {
                      // 意向城市数据
                      if (citys.intentionalPlace) {
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null /g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          / null/g,
                          ""
                        );
                        citys.intentionalPlace = citys.intentionalPlace.replace(
                          /null/g,
                          ""
                        );
                        if (citys.intentionalPlace == " ") {
                          citys.intentionalPlace = citys.intentionalPlace;
                        } else {
                          citys.intentionalPlace = citys.intentionalPlace.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  }
                  that.applyTime.push(itemList.applyTime); // 投递时间
                  if (itemList.resume.isSeen == "02") {
                    that.textCont.push("已关注");
                  } else if (itemList.resume.isSeen == "01") {
                    that.textCont.push("感兴趣");
                  }

                  if (itemList.resume.intentionPosition) {
                    itemList.resume.intentionPosition.forEach(positions => {
                      // 意向岗位数据
                      if (positions.positionName) {
                        positions.positionName = positions.positionName.replace(
                          /null /g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          / null/g,
                          ""
                        );
                        positions.positionName = positions.positionName.replace(
                          /null/g,
                          ""
                        );
                        if (positions.positionName == " ") {
                          positions.positionName = positions.positionName;
                        } else {
                          positions.positionName = positions.positionName.replace(
                            / /g,
                            "/"
                          );
                        }
                      }
                    });
                  }
                  // 招聘类型(1:社会工、2:实习工、3:临时工、4:不限、5:小时工、6:寒假工、7:暑假工、8：学生工)
                  if (itemList.demand.recruitDemandType) {
                    if (itemList.demand.recruitDemandType == 1) {
                      itemList.demand.recruitDemandType = "社会工";
                    }
                    if (itemList.demand.recruitDemandType == 2) {
                      itemList.demand.recruitDemandType = "实习工";
                    }
                    if (itemList.demand.recruitDemandType == 3) {
                      itemList.demand.recruitDemandType = "临时工";
                    }
                    if (itemList.demand.recruitDemandType == 4) {
                      itemList.demand.recruitDemandType = "不限";
                    }
                    if (itemList.demand.recruitDemandType == 5) {
                      itemList.demand.recruitDemandType = "小时工";
                    }
                    if (itemList.demand.recruitDemandType == 6) {
                      itemList.demand.recruitDemandType = "寒假工";
                    }
                    if (itemList.demand.recruitDemandType == 7) {
                      itemList.demand.recruitDemandType = "暑假工";
                    }
                    if (itemList.demand.recruitDemandType == 8) {
                      itemList.demand.recruitDemandType = "学生工";
                    }
                  }
                  var objResume = itemList.resume;
                  for (var element in objResume) {
                    if (element) {
                      if (
                        objResume[element] == "" ||
                        objResume[element] == null ||
                        objResume[element] == []
                      ) {
                        objResume[element] = "暂无";
                      }
                    } else {
                      console.log("缺失字段");
                    }
                  }
                  var objDemand = itemList.demand;
                  for (var i in objDemand) {
                    if (i) {
                      if (
                        objDemand[i] == "" ||
                        objDemand[i] == null ||
                        objDemand[i] == []
                      ) {
                        objDemand[i] = "暂无";
                      }
                    } else {
                      console.log("缺失字段");
                    }
                  }
                });
                that.optionResume = that.postitionList.concat(
                  drlInfinitedListDataArr
                );
                $state.loaded();
              } else {
                $state.complete();
              }
            } else {
            }
          })
          .catch(error => {});
      } else {
        $state.complete();
      }
    }
  },
  props: []
};
</script>

<style lang="css" src='./style/commonList.css' scoped></style>
<style scoped>
.deliver_position {
  display: inline-block;
  width: 11.625rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.flex_layout {
  display: flex;
  display: -webkit-flex;
  align-items: center;
}

.deliverTxt_w {
  flex-shrink: 0;
}

/* 空页面样式 */
.blank_page {
  width: 100%;
  height: 100%;
  padding-top: 9.71875rem;
  box-sizing: border-box;
  text-align: center;
}

.blank_page > img {
  width: 12.40625rem;
  margin: 0 auto 1.09375rem;
}

.blank_page > p {
  color: #354242;
  font-size: 0.875rem;
  width: 12.6875rem;
  text-align: center;
  margin: 0 auto;
  line-height: 1.375rem;
}

/* 公共样式 */
.flex_layOut {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  justify-content: space-between;
}

.c-354242 {
  color: #354242;
}

.f-15 {
  font-size: 0.9375rem;
}

.mr-8 {
  margin-right: 0.5rem;
}

.ml-5 {
  margin-left: 0.3125rem;
}

.ml-10 {
  margin-left: 0.625rem;
}

.mb-19 {
  margin-bottom: 0.59375rem !important;
}

.mb-21 {
  margin-bottom: 0.65625rem !important;
}

.text-center {
  text-align: center;
}
.auth-no-success {
  padding-top: 9.5rem;
  margin: auto 0;
  text-align: center;
}
.auth-no-success img {
  display: inline-block;
  width: 12.15625rem;
  height: 6.96875rem;
}
.auth-no-success p {
  padding-top: 1.9375rem;
  font-size: 14px;
  color: #354242;
}
.lh-1 {
  line-height: 1;
}

.dspFlex {
  display: flex;
  display: -webkit-flex;
}
</style>



