<template>
  <!-- 从事行业modal -->
  <div class="slideUpModal">
    <transition enter-active-class="slideInUp" leave-active-class="fadeOutDown">
      <div class="animated faster modal_cnt" v-show="slideUpModalShow">
        <p class="top_title">
          <span class="f-32 00241f" @click="cancelBtnClk">取消</span>
          <span class="f-32 c-blue" @click="ensureBtnClk">确认</span>
        </p>
        <div class="list_box">
          <div class="listLeft_box" ref="listLeft_box">
            <ul>
              <li :class="{on : resetBtnflag}" @click="resetBtnClk">不限</li>
              <li
                :class="{on:selectVal == item.value}"
                @click="listEachClk(item.children,item.value,item.label,index)"
                v-for="(item,index) in positionTypeArr"
                :key="index"
              >{{item.label}}</li>
            </ul>
          </div>
          <div class="listRight_box" ref="listRight_box" v-if="positionTypeArrTwo">
            <ul>
              <li
                :class="{on:selectTwoVal == item.value}"
                @click="listEachClkTwo(item.children,item.value,item.label,index)"
                v-for="(item,index) in positionTypeArrTwo"
                :key="index"
              >{{item.label}}</li>
            </ul>
          </div>
          <!-- <div class="listRight_box" ref="listRight1_box" v-if="positionTypeArrThree">
             <ul>
              <li
                :class="{on:selectthreeVal == item.value}"
                @click="listEachClkThree(item.children,item.value,item.label,index)"
                v-for="(item,index) in positionTypeArrThree"
                :key="index"
              >{{item.label}}</li>
            </ul>
          </div>-->
        </div>
      </div>
    </transition>
    <div class="mask" v-show="maskShow" @click="maskClk"></div>
  </div>
</template>

<script>
import Util from "@/js/mUtils";
import { DLDict, DLArea, DLPType, DLIndustry } from "@/js/dl-code";
export default {
  data() {
    return {
      globalApiUrl: domain.testUrl,
      positionTypeArr: [], // 岗位类型三级联动数据
      positionTypeArrTwo: [],
      positionTypeArrThree: [],
      selectVal: "",
      selectTwoVal: "",
      selectthreeVal: "",
      slideUpModalShow: true, // modal默认显示
      maskShow: true, // mask默认显示
      listArr: [],
      iswIndex: this.selectActiveIndexIs, // 存储telentSearch传递值
      resetBtnflag: false // 不限按钮点击标记
    };
  },
  mounted() {
    this.getPositionDate(); // 岗位类型三级联动
  },
  methods: {
    getPositionDate() {
      //岗位类型三级联动数据
      var token = Util.getCookie("qdsMToken");
      let that = this;
      that
        .axios(
          that.globalApiUrl +
            "api/recruitManager/findAllCodePost?token=" +
            token,
          {
            method: "get",
            params: {
              type: 1
            }
          }
        )
        .then(function(response) {
          if (response.data && response.data.length > 0) {
            var _resArr = response.data;
            _resArr.forEach((item, index) => {
              //用item操作每一条数据。
              var child = {
                value: item.code,
                label: item.name,
                children: []
              };
              that.positionTypeArr.push(child);
              var _resArr2 = _resArr[index].childrenList;
              _resArr2.forEach((item2, index2) => {
                //用item操作每一条数据。
                var child2 = {
                  value: item2.code,
                  label: item2.name,
                  children: []
                };
                that.positionTypeArr[index].children.push(child2);
                var _resArr3 = _resArr2[index2].childrenList;
                _resArr3.forEach((item3, index3) => {
                  //用item操作每一条数据。
                  var child3 = {
                    value: item3.code,
                    label: item3.name
                  };
                  that.positionTypeArr[index].children[index2].children.push(
                    child3
                  );
                });
              });
            });
          }
        })
        .catch(function(error) {});
    },
    maskClk() {
      // maks点击事件
      this.slideUpModalShow = false;
      this.maskShow = false;
      this.$emit("slideModalMaskFn", 1); // 自定义事件slideModalMask,往searchResume组件传值,初始化tabActiveIndex
    },
    // li点击事件
    listEachClk(select, value, label, index) {
      (this.resetBtnflag = false), // 移除不限颜色
        (this.selectVal = value); // value选中的职位code
      this.positionTypeArrTwo = [];
      this.positionTypeArrThree = [];
      if (select.length > 0) {
        this.positionTypeArrTwo = select;
      }
    },
    listEachClkTwo(select, value, label, index) {
      this.selectTwoVal = value;
      this.positionTypeArrThree = [];
      if (select.length > 0) {
        this.positionTypeArrThree = select;
      }
    },
    // listEachClkThree(select,value,label,index) {
    //   this.selectthreeVal = value
    // },
    // modal取消按钮点击事件
    cancelBtnClk() {
      this.slideUpModalShow = false;
      this.maskShow = false;
      this.$emit("slideModalMaskFn", 1); // 自定义事件slideModalMask,往searchResume组件传值,初始化tabActiveIndex
    },
    // modal确定按钮点击事件
    ensureBtnClk() {
      this.slideUpModalShow = false;
      this.maskShow = false;

      //  选中的二级职位code
      var secondSelectPositionCode = this.selectTwoVal;
      this.$emit("slideModalMaskFn", {
        Id: 1,
        pstVal: secondSelectPositionCode
      });
    },
    // 不限相当于重置按钮
    resetBtnClk() {
      this.resetBtnflag = true; // 点击状态
      this.positionTypeArrTwo = []; // 重置职位二级列表
      this.selectVal = 0;

      this.selectTwoVal = "";
    }
  }
};
</script>

<style lang="css" src='./style/animate.css' scoped></style>


