<template>
  <!-- 人才管理模块 -->
  <div class="searchResume_wrapper" ref="searchResume_wrapper">
    <!-- tab -->
    <mt-navbar v-model="selected" fixed>
      <mt-tab-item id="1">
        <span @click="myResumeTabClk">我的简历</span>
      </mt-tab-item>
      <mt-tab-item id="2">
        <span @click="deliverRecoredTabClk">投递记录</span>
      </mt-tab-item>
      <mt-tab-item id="3">
        <span @click="telentsRearchTabClk">人才搜索</span>
      </mt-tab-item>
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <!-- 我的简历 -->
      <mt-tab-container-item id="1">
        <myResumeList-mdl></myResumeList-mdl>        
      </mt-tab-container-item>
      <!-- 投递记录 -->
      <mt-tab-container-item id="2">
        <deliverRecoredList-mdl></deliverRecoredList-mdl>
      </mt-tab-container-item>
      <!-- 人才搜索 -->
      <mt-tab-container-item id="3">
        <telentSearchList-mdl></telentSearchList-mdl>
      </mt-tab-container-item>
    </mt-tab-container>
    <!-- 底部tab -->
    <nav-tab :fatherActive="active"></nav-tab>
  </div>
</template>
<script>
import Vue from "vue";
import navTab from "@/components/layout/tab.vue"; // 底部tab组件
import { Navbar, TabItem } from "mint-ui"; // mint-ui tab
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
import myResumeList from "@/views/resume/myResumeList"; // 列表组件
import InfiniteLoading from "vue-infinite-loading"; // 无限滚动加载组件
import deliverRecoredList from "@/views/resume/deliverRecoredList"; // 投递记录列表页模块
import telentSearchList from "@/views/resume/telentSearchList"; // 人才搜索列表模块

export default {
  data() {
    return {      
      selected: "3", // tab-container 默认人才搜索
      active: "0" // 底部tab 默认人才管理
    };
  },
  mounted() {
    let dliverRcdDtialIndex = this.$route.query.id; // 由投递记录详情页跳转,路由参数2
    if (this.$route.query.selected) {
      this.selected = this.$route.query.selected;
    }
    if (dliverRcdDtialIndex == 2) {
      this.selected = "2"; // 显示投递记录
    } else if (dliverRcdDtialIndex == 3) {
      this.selected = "3"; // 显示人才列表
    }else if(dliverRcdDtialIndex == 1){
       this.selected = "1"; // 显示我的简历列表
    }
  },
  components: {
    navTab, // tab组件注册
    "myResumeList-mdl": myResumeList, // 我的简历列表组件注册
    InfiniteLoading, // 滚动无限加载组件
    "deliverRecoredList-mdl": deliverRecoredList, // 投递记录模块组件组册
    "telentSearchList-mdl": telentSearchList // 人才搜索模块组件注册
  },
  methods: {
    // 我的简历tab点击事件
    myResumeTabClk() {
      this.$refs.searchResume_wrapper.style.paddingTop = 2.4375 + "rem"; // padding-top恢复默认
    },
    // 投递记录tab点击事件
    deliverRecoredTabClk() {
      this.$refs.searchResume_wrapper.style.paddingTop = 2.4375 + "rem"; // padding-top恢复默认
    },
    // 人才搜索tab点击事件
    telentsRearchTabClk() {
      // this.$refs.searchResume_wrapper.style.paddingTop = 0;
    }
  }
};
</script>

<style lang="css" src='./style/commonList.css' scoped></style>
<style scoped>
.searchResume_wrapper {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
  padding-top: 2.4375rem;
  box-sizing: border-box;
}
</style>





